<script>
    import LeadDataService from "../../../services/DataService";
export default {   
    props: { 
        leadId:null,
        moduleName:null,
        subModuleCode:null,
        callFrom:null
},
    data: function () {
        return {
            isLoading:false,
            noRecordLeadConvert: false,
            leadConvertContactsData: null,
            LeadConvertContactsHeader: null,
            afterConvertToAccount: false,
            labelForAdd: '',
            isSelected: false,
            isErrorMessage: '',
            accountName: '',
            SortBy: '',
            SortExp: '',
            PageSize: 50,
            PageNumber: 1,
            dealData: [],
            GroupBy: [],
            isConvertLeadToOthers: false,
            createdAccountID: 0,          
            successMessage: '',
            contactListForAddDeal: [],
            saveFor: '',
            submited: false,
            clientData: [],
            ProjectTemplateListJson: [],
            clientCheck: -1,
            popupHeader: 'Convert Lead',
         
            Duplicate: '0',
            existName: '',
            SaveDisabled: false,
            projectDetail: {
                ProjectName: '',
                ProjectTemplate: ''
            },
            DependentOption: [{
              
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            },
            {
              
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date(Date.now())
            }
            ],
            isProjectShow: false,
            isContactForExistsAccount: true,
            LeftViewData: [],
            AccName: '',
            NewAccName: '',
            ACCountDDL: [],
            OldAccountId: 0,
            IsActiveAccount: 0,
            contactId: 0,
            LeadStatus: '',
            LeadCompany: '',
            Leadaddr1: '',
            Leadaddr2: '',
            Leadcity: '',
            Leadzip: '',
            Leadstate: '',
            contactsData: [],
            contactsHeader: [],
            noRecordForTab: false,
            dataToConvertLead: {},
            leadName: "",
            clienFirstName: "",
            clientLastName: "",
            clientaddr1: "",
            clientaddr2: "",
            clientcity: "",
            clientstate: "",
            clientzip: "",
            clientcompany: "",
            defaultemailid: null,        
            popover: {
                visibility: 'click'
            }    
        }
    },
  
    created: function () {   

        this.fetchData();     
        this.fetchConvertLeadContacts();
        this.accountName = this.leadName;
        this.GetProjectTemplatelist();
        this.fetchContacts();
    },
    methods: {
        onBlur(ths,name,display_name){            
            setTimeout(() => {
                var value = $(`div[name="${name}"]`).attr('value');
                if (value) {
                    $(ths.target).removeClass('is-invalid');
                    $(ths.target).parent().next().addClass('d-none');
                } else {
                    $(ths.target).addClass('is-invalid');
                    if ($(ths.target).parent().next().is('span')) {
                        $(ths.target).parent().next().removeClass('d-none');
                    } else {
                        $(ths.target).parent().parent().append(`<span name="${name}" class="invalid-feedback d-block">${this.GetFormSchemaFieldName(name, display_name)} is required.</span>`);
                    }
                }
            }, 700);         
        },
        fetchData(){
         
           var apiUrl=`id=${this.leadId}&refId=${this.leadId}&moduleName=${this.moduleName}&subModuleCode=${this.subModuleCode}`;
           var vm = this;
           vm.isLoading=true; 
      LeadDataService.FormLayoutView(apiUrl).then(function (response) {
        vm.isLoading=false; 
        var fullLeftJSON = response.data.LEFTDATA;
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return (element.form_field_visibility == 'YES');
                    });
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        $.grep(fullLeftJSON, function (element, index) {
          if (element.name == "role_title_id") {
            vm.dataToConvertLead.roletitle =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "home_phone") {
            vm.dataToConvertLead.home_phone = element.value;
          }
          if (element.name == "business_phone") {
            vm.dataToConvertLead.business_phone = element.value;
          }
          if (element.name == "contact_number") {
            vm.dataToConvertLead.contact_number = element.value;
          }
          if (element.name == "account_id") {
            vm.dataToConvertLead.account_id = element.value;
          }
          if (element.name == "channel_id") {
            vm.dataToConvertLead.lead_source_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "industry_type_id") {
            vm.dataToConvertLead.industry_type_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_country") {
            vm.dataToConvertLead.lead_country =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "lead_state") {
            vm.dataToConvertLead.lead_state =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "external_source_id") {
            vm.dataToConvertLead.referred_by =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "status_id") {
            vm.LeadStatus = vm.FilterDDLandNormalValue(element.value);
          } else if (element.name == "client_first_name") {
            vm.clienFirstName = element.value != null ? element.value : "";
          } else if (element.name == "client_last_name") {
            vm.clientLastName = element.value != null ? element.value : "";
          } else if (element.name == "lead_address1") {
            vm.clientaddr1 = element.value != null ? element.value : "";
          } else if (element.name == "lead_address2") {
            vm.clientaddr2 = element.value != null ? element.value : "";
          } else if (element.name == "lead_city") {
            vm.clientcity = element.value != null ? element.value : "";
          } else if (element.name == "lead_state") {
            vm.clientstate =
              element.value != null ? JSON.parse(element.value)[0].name : "";
          } else if (element.name == "lead_zipcode") {
            vm.clientzip = element.value != null ? element.value : "";
          } else if (element.name == "lead_company_name") {
            vm.clientcompany = element.value != null ? element.value : "";
          } else if (element.name == "client_email") {
            vm.defaultemailid = element.value != null ? element.value : "";
          } else if (element.name == "expected_close_date") {
            vm.dataToConvertLead.closing_date = element.value;   
                    
          } else if (element.name == "money_value") {
            vm.dataToConvertLead.ammount = element.value;
          } else if (element.name == "campaign_id") {
            vm.dataToConvertLead.campaign_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "lead_owner_id") {
            vm.dataToConvertLead.lead_owner = element.value;
          }
          if (
            element.name == "commission_type" &&
            $.trim(element.value) != ""
          ) {
            element.form_field_visibility = "YES";
            element.display_order = 20;
          }
          if (element.name == "agreement_type" && $.trim(element.value) != "") {
            element.form_field_visibility = "YES";
            element.display_order = 19;
          }
          
        });
        if (vm.leadName == "") {
          vm.leadName = vm.clienFirstName + " " + vm.clientLastName;
        }       
        vm.dataToConvertLead.lead_name = vm.leadName;
                    }        
           
                             
      });
        },
        GetLocalizedValue: function (fieldName, displayName) {
            try {
                if (this.isBlank(displayName)) {
                    var chk = this.$t(String(fieldName).toUpperCase());
                    if (chk != String(fieldName).toUpperCase()) {
                        return chk;
                    }
                    chk = this.$t(String(fieldName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return fieldName;
                }
                else {
                    var chk = this.$t(String(displayName).toUpperCase());
                    if (chk != String(displayName).toUpperCase()) {
                        return chk;
                    }
                    chk = this.$t(String(displayName));
                    if (chk || /^\s*$/.test(chk)) {
                        return chk;
                    }
                    return displayName;
                }
            }
            catch (ex) { console.log(ex) }
        },
        ChangeAccount: function (IsActive) {
            if (IsActive == "2")
                this.createdAccountID = 0
            else
                this.createdAccountID = this.OldAccountId;
        },
        fetchContacts: function () {
                var vm = this;
               vm.isLoading=true;
                var param = `sourceid=${vm.leadId}&submodulecode=CRM_LEADS`;                   
                        LeadDataService.PrimaryContactDetails(param)
                        .then(function (response) { 
                            vm.isLoading=false;                                                    
                            if (response.data != null) {
                                vm.contactsData = response.data.data;
                                vm.contactsHeader = response.data.schema;
                                setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                                vm.noRecordForTab = false;
                            } else {
                                vm.noRecordForTab = true;
                                vm.contactsData = null;
                            }
                        });
            },
        getName: function (DdlAcc, OldAccountId) {
            var option = DdlAcc.filter(v => v.value == OldAccountId);
            this.AccName = option[0].name;
            this.createdAccountID = OldAccountId
        },
        GetAssociatedAccounts: function (contactID) {
                var vm = this;
                
                var param =`id=&moduleName=CRM&type=CONTACT_ACCOUNTS&refId=${contactID}` 
                LeadDataService.getDDLData(param)
                .then(function (response) {
                    var jsonD = [];
                    if (response.data != null && response.data != "") {
                        jsonD = response.data.DATA;
                    }
                    vm.ACCountDDL = jsonD;
                    if (jsonD.length > 0) {
                        vm.OldAccountId = jsonD[0];
                        vm.AccName = jsonD[0].name;
                        if (vm.createdAccountID > 0) {
                            vm.OldAccountId = vm.createdAccountID;
                            vm.IsActiveAccount = 2;
                            var Listdata = jsonD.filter(v => v.value == vm.createdAccountID);
                            if (Listdata != null && Listdata.length > 0) {
                                vm.AccName = Listdata[0].name;
                            }
                            else if (jsonD != null && jsonD.length > 0) {
                                vm.createdAccountID = jsonD[0];
                                vm.AccName = jsonD[0].name;
                            }
                        }
                        else {
                            vm.createdAccountID = vm.OldAccountId;
                            vm.IsActiveAccount = 1;
                        }
                    }
                    else
                        vm.IsActiveAccount = 1;
                     
                });
            },
        DependentDateObject: function (item, date) {            
            if (item.name == 'contract_start_date') {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate = new Date(Date.now());
                }
                this.DependentOption[0] = {
                    format: null,
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
                $.each(this.GroupBy, function (filedIndex, fieldItem) {
                    $.each(fieldItem.Data, function (index, element) {
                        if (element.name == "contract_end_date") {
                            element.value = '';
                        }
                    });
                });
            } else if (item.name == 'billing_start_date') {
                var checkDate = new Date();
                if ((typeof date !== 'undefined') && date != '') {
                    checkDate = new Date(Date.parse(date));
                }
                else {
                    checkDate = new Date(Date.now());
                }
                this.DependentOption[1] = {
                    format: null,
                    useCurrent: false,
                    showClear: true,
                    showClose: true,
                    allowInputToggle: true,
                    minDate: checkDate
                }
                $.each(this.GroupBy, function (filedIndex, fieldItem) {
                    $.each(fieldItem.Data, function (index, element) {
                        if (element.name == "billing_end_date") {
                            element.value = '';
                        }
                    });
                });
            }      
        var vm=this;        
        setTimeout(function(){
            var ele=$(`div[name="${item.name}"]`);
            var eleval=$(`div[name="${item.name}"] input:first`);
                var value=ele.attr('value');
                if (value) {
                    eleval.removeClass('is-invalid');
                    eleval.parent().parent().find(`span[name='${item.name}']`).removeClass('d-block').addClass('d-none');
                } else {
                    eleval.addClass('is-invalid');
                    if (eleval.parent().next().is('span')) {
                        eleval.parent().next().removeClass('d-none');
                    } else {
                        eleval.parent().parent().append(`<span name="${item.name}" class="invalid-feedback d-block">${vm.GetFormSchemaFieldName(item.name, item.display_name)} is required.</span>`);
                    }
                }            },200);


        },
        HideMessage: function () {
            this.Duplicate = 0;
            $("input[name='ProjectName']").removeClass('is-invalid');
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },       
        GetProjectTemplatelist: function () {            
                var vm = this;
                var url = `id=&moduleName=CRM&type=PROJECT`;
                LeadDataService.getDDLData(url)
                .then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        vm.ProjectTemplateListJson = json.DATA;
                        return vm.ProjectTemplateListJson;
                    }
                });
            },     
            onSearch: function onSearch(search, objItem, selctedValue) {
            this.searchSelectData(search, this, objItem, selctedValue);
        },
        searchSelectData:function(search, vm, objItem, selctedValue){
            this.ShowSearchData(search, vm, objItem, selctedValue);
        },
        ShowSearchData: function (search, vm, objItem, selctedValue) {
                if (objItem.name == 'contact_id' && dt_code == 'select' && vm.contactListForAddDeal != null) {
                    var filteredObj = [];
                    filteredObj = $.grep(vm.contactListForAddDeal, function (n, i) {
                        return n.first_name.includes(search);
                    });
                    return objItem.select_options == filteredObj;
                }
                if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
                var url = "id=&moduleName="+vm.moduleName+"&type=" + objItem.field_code;
                if (objItem.dependent_type == 'fill') {
                    url += "&refId=" + selctedValue;
                }                
                LeadDataService.getDDLData(url + "&search=" + escape(search)).then(function (response) {
                    var json = response.data;
                    if (json != null) {
                        objItem.select_options = json.DATA;
                        return objItem.select_options;
                    }
                });
            },
        setSelectedValue: function (item, val) {
            var vm = this;
            try {
                if (Array.isArray(val)) {
                    item.value = (typeof val[0] === 'object' && val[0] !== null) ? val[0].value : val[0];
                }
                else {
                    if (item.has_dependent > 0) {
                        $.each(vm.GroupBy, $.proxy(function (key, parentItem) {
                            $.each(parentItem.Data, function (childKey, childItem) {
                                if (childItem.parent_id == item.custom_field_id && childItem.dependent_type == 'fill') {
                                    vm.ShowSearchData('', vm, childItem, val.value);
                                }
                            });
                        }, this));
                    }
                    item.value = val;
                }
            }
            catch (ex) {
            }
        },
        fetchConvertLeadContacts: function () {
            var vm = this;
            vm.isLoading=true; 
            var accountID = ($.trim(vm.dataToConvertLead.account_id) != '' ? vm.dataToConvertLead.account_id : '');
            var subModuleCode = '';
            var sourceId = '';
            vm.createdAccountID = 0;
            if (accountID != '' && accountID > 0) {
                vm.createdAccountID = accountID;
                sourceId = accountID;
            }
            subModuleCode = vm.subModuleCode;
            sourceId =  vm.leadId;           
            var url = "id=" + sourceId + "&subModuleCode=" + subModuleCode + "&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber;
                    var url1 = "id=" + vm.leadId + "&moduleName=CRM&subModuleCode=CRM_LEADS";                  
                    LeadDataService.ContactListing(url).then(function (response) {
                        vm.isLoading=false; 
                if (response.data != null) {                  
                    vm.LeadConvertContactsHeader = response.data.schema;
                    if (response.data.data != null && response.data.data.length > 0) {
                        var PrimaryContact = response.data.data.filter(v => v.IsPrimary == 1)
                        if (PrimaryContact != null && PrimaryContact.length > 0)
                            vm.contactId = PrimaryContact[0].contact_id;
                        else {
                            vm.contactId = response.data.data[0].contact_id;
                            response.data.data[0].IsPrimary = true;
                        }
                        vm.leadConvertContactsData = response.data.data;
                        setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                       vm.noRecordLeadConvert = false;
                         
                    } else {
                        vm.noRecordLeadConvert = true;
                    }
                } else {
                    vm.noRecordLeadConvert = true;
                }
            });
            LeadDataService.FormLayoutView(url1).then(function (response) {             
                if (response.data != null) {
                    var fullLeftJSON = response.data.LEFTDATA;
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return (element.form_field_visibility == 'YES');
                    });
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        var First_Name = '';
                        var Last_Name = '';
                        var add1 = '';
                        var add2 = '';
                        var city1 = '';
                        var state1 = '';
                        var stateval = '';
                        var zipcode1 = '';
                        var leadcompany = '';
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    if (element.name == "client_first_name") First_Name = element.value;
                                    if (element.name == "client_last_name") Last_Name = element.value;
                                    if (element.name == "lead_address1") add1 = element.value;
                                    if (element.name == "lead_address2") add2 = element.value;
                                    if (element.name == "lead_city") city1 = element.value;
                                    if (element.name == "lead_company_name") leadcompany = element.value;
                                    if (element.name == "lead_state") {
                                        stateval = JSON.parse(element.value) == null ? "" : JSON.parse(element.value)[0].value;
                                        var dataState = JSON.parse(element.value);
                                        if (dataState != null && dataState.length > 0)
                                            state1 = dataState[0].name
                                    }
                                    if (element.name == "lead_zipcode") zipcode1 = element.value;
                                    if (element.group_id == groupId && (element.name == "client_first_name" || element.name == "client_last_name" || element.name == "client_email" || element.name == "contact_number" || element.name == "home_phone" || element.name == "business_phone")) {
                                        return element;
                                    }
                                   
                                    vm.LeadCompany = leadcompany;
                                    vm.Leadaddr1 = add1;
                                    vm.Leadaddr2 = add2;
                                    vm.Leadcity = city1;
                                    vm.Leadzip = zipcode1;
                                    vm.Leadstate = stateval;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, this));

                        if (leadcompany == '' || leadcompany == null) {
                            vm.NewAccName = $.trim($.trim(First_Name) + ' ' + $.trim(Last_Name) + ' ' + $.trim(leadcompany) + ' ' + $.trim(add1) + ' ' + $.trim(add2) + ' ' + $.trim(city1) + ' ' + $.trim(state1) + ' ' + $.trim(zipcode1));
                        }
                        else {
                            vm.NewAccName = $.trim(leadcompany);
                        }
                        vm.NewAccName = vm.NewAccName.replace(/ +(?= )/g, '');
                
                    }
                     
                } else {
                    vm.noRecordLeadConvert = true;
                }
                setTimeout(function () {
                    vm.GetAssociatedAccounts(vm.contactId)
                }, 400);
            });
        },
        getLeadContactsIFAssociatedAccountDontHaveContacts: function (sourceId, subModuleCode) {
            var vm = this;
            if (vm.createdAccountID > 0) {
                var url = "id=" + sourceId + "&subModuleCode=" + subModuleCode + "&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber;
                LeadDataService.ContactListing(url).then(function (response) {
                    if (response.data != null) {
                        vm.LeadConvertContactsHeader = response.data.schema;
                        if (response.data.data != null && response.data.data.length > 0) {
                            vm.leadConvertContactsData = response.data.data;
                            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                            vm.afterConvertToAccount = true;
                            vm.convertLead();
                            vm.noRecordLeadConvert = false;
                        } else {
                            vm.noRecordLeadConvert = true;
                        }
                    } else {
                        vm.noRecordLeadConvert = true;
                    }
                     
                });
            }
        },
        getClientData: function (value) {
            var textValue = {
                user_id: 0,
                first_name: '',
                last_name: '',
                email_id: '',
                account_id: value
            };
            this.clientData.push(textValue);
        },
        FetchData: function (value, CheckType) {
          
            var contactIds = []
            var vm = this;
            vm.isLoading=true;
            if (vm.createdAccountID > 0) {
                vm.contactListForAddDeal = [];
                $.each(vm.leadConvertContactsData, function (filedIndex, fieldItem) {
                    var varObject = {
                        "name": fieldItem.first_name + ' ' + fieldItem.last_name,
                        "value": fieldItem.contact_id,
                        "first_value": null,
                        "second_value": null
                    }
                    contactIds.push(fieldItem.contact_id);
                    vm.contactListForAddDeal.push(varObject);
                });
            } else {
                $("#ConvertLeadContacts table tr td input[type='checkbox']:checked").each(function () {
                    var checkboxObj = this
                    contactIds.push($(checkboxObj).val());
                    var filteredObj = [];
                    filteredObj = $.grep(vm.leadConvertContactsData, function (n, i) {
                        return n.contact_id == $(checkboxObj).val();
                    });
                    if (filteredObj.length > 0) {
                        var varObject = {
                            "name": filteredObj[0].first_name + ' ' + filteredObj[0].last_name,
                            "value": filteredObj[0].contact_id,
                            "first_value": null,
                            "second_value": null
                        }
                        vm.contactListForAddDeal.push(varObject);
                    }
                });
               
            }
             
            var otherData;
            var subModuleCode = '';
            if (CheckType == 'DEAL') {
                vm.isProjectShow = false;
                otherData = [{ id: value, name: 'account_id' }, { id: vm.FilterDDLValueForID(vm.dataToConvertLead.lead_owner), name: 'deal_owner_id' }];
                vm.labelForAdd = '{{$t("Deal ")}} {{$t("Deal ")}}';
                subModuleCode = 'CRM_DEALS'
            } else if (CheckType == 'CONTRACT') {
                vm.isProjectShow = true;
                otherData = [{ id: value, name: 'account_id' }];
                vm.labelForAdd = 'AddContract';
                subModuleCode = 'CRM_CONTRACTS';
            }
            vm.saveFor = CheckType;
            var convertJson = encodeURIComponent(JSON.stringify(otherData))
            var requestUrl ="moduleName=CRM&subModuleCode=" + subModuleCode + "&otherData=" + convertJson;
            LeadDataService.getFormLayout(requestUrl)
                .then(function (response) {
                    vm.isLoading=false;
                vm.GroupBy = [];
                vm.dealData = response.data.data;
                if (vm.dealData != null && vm.dealData.length > 0) {
                    var groupId = vm.dealData[0].group_id;
                    $.each(vm.dealData, $.proxy(function (index, item) {
                        groupId = item.group_id;
                        var chkGroup = $.grep(vm.GroupBy, function (element) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vm.dealData, function (element) {
                                return element.group_id == groupId;
                            });
                            if (value > 0) {
                                $.each(returnedData, function (filedIndex, fieldItem) {
                                    if (fieldItem.dt_code == "select") {
                                        try {
                                            fieldItem.value = fieldItem.value.trim();
                                        } catch (ex) { console.log("value is null while trimming") }
                                    }
                                    if (fieldItem.picklist_options == "Lookup") {
                                        fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                        fieldItem.select_value = fieldItem.select_options;
                                        if (fieldItem.name == 'account_id') {
                                            if(CheckType=="CONTRACT")
                                            {
                                                fieldItem.value = fieldItem.select_options.find(x=>x.value==value)
                                            }
                                            else
                                            {
                                                fieldItem.value = fieldItem.select_options.find(x=>x.value==value);
                                            }
                                        }
                                    }
                                    if (fieldItem.picklist_options == "Lookup" && fieldItem.dropdown_type=='custom' && fieldItem.select_options && fieldItem.value=='') {                                  
                                            fieldItem.value = fieldItem.select_options[0];                                        
                                    }
                                    if (fieldItem.name == 'lead_id') { fieldItem.value = vm.leadId; }
                                    if (CheckType == 'DEAL') {                                       
                                        if (fieldItem.name == 'closing_date') { 
                                            fieldItem.value = (vm.dataToConvertLead.closing_date != null && vm.dataToConvertLead.closing_date.length > 0) ? vm.dataToConvertLead.closing_date : '';
                                        }
                                        if (fieldItem.name == 'deal_name') {                                           
                                            fieldItem.value = vm.IsActiveAccount == 1 ? vm.NewAccName : vm.AccName;
                                        }
                                        if (fieldItem.name == 'deal_name') {                                           
                                            fieldItem.value = vm.dataToConvertLead.lead_name;
                                        }
                                        if (fieldItem.name == 'email_id') {                                           
                                            fieldItem.value = vm.dataToConvertLead.email_id;
                                        }
                                        if (fieldItem.name == 'amount') {
                                            fieldItem.value = (vm.dataToConvertLead.ammount != null && vm.dataToConvertLead.ammount.length > 0) ? vm.dataToConvertLead.ammount : '';
                                        }
                                        if (fieldItem.picklist_options == "Lookup" && fieldItem.name == 'status_id') {
                                            fieldItem.value = 1001;
                                        }
                                        if (fieldItem.name == 'campaign_id') {
                                            fieldItem.value = vm.dataToConvertLead.campaign_id;
                                        }
                                        if (fieldItem.picklist_options == "Lookup" && fieldItem.name == 'contact_name') {
                                            fieldItem.select_options = vm.contactListForAddDeal;
                                            if (vm.contactsData != null && vm.contactsData.length > 0) {
                                                fieldItem.value = vm.contactsData[0].contact_id;
                                            }
                                        }
                                        else if (fieldItem.picklist_options == "Lookup" && fieldItem.name == 'lead_source_id') {
                                            fieldItem.value = vm.dataToConvertLead.lead_source_id;
                                        }
                                        else if (fieldItem.picklist_options == "Lookup" && fieldItem.name == 'referred_by') {
                                            fieldItem.value = vm.dataToConvertLead.referred_by;
                                        }                                        
                                        else if (fieldItem.name == 'deal_address1') { fieldItem.value = vm.clientaddr1; }
                                        else if (fieldItem.name == 'deal_address2') { fieldItem.value = vm.clientaddr2; }
                                        else if (fieldItem.name == 'deal_city') { fieldItem.value = vm.clientcity; }
                                        else if (fieldItem.name == 'deal_state') { fieldItem.value = vm.dataToConvertLead.lead_state; }
                                        else if (fieldItem.name == 'deal_zipcode') { fieldItem.value = vm.clientzip; }
                                        else if (fieldItem.name == 'deal_country') { fieldItem.value = vm.dataToConvertLead.lead_country; }
                                        else if (fieldItem.name == 'deal_company_name') { fieldItem.value = vm.clientcompany; }
                                        else if (fieldItem.name == 'industry_type_id') { fieldItem.value = vm.dataToConvertLead.industry_type_id; }
                                        else if (fieldItem.name == 'role_title_id') { fieldItem.value = vm.dataToConvertLead.roletitle; }
                                        else if (fieldItem.name == 'email_id') { fieldItem.value = vm.defaultemailid; }
                                        else if (fieldItem.name == 'home_phone') { fieldItem.value = vm.dataToConvertLead.home_phone; }
                                        else if (fieldItem.name == 'business_phone') { fieldItem.value = vm.dataToConvertLead.business_phone; }
                                        else if (fieldItem.name == 'mobile_phone') { fieldItem.value = vm.dataToConvertLead.contact_number; }
                                    }
                                    else if (CheckType == 'CONTRACT') {
                                        if (fieldItem.name == 'contract_start_date' || fieldItem.name == 'contract_end_date') {
                                            fieldItem.value = (vm.dataToConvertLead.closing_date != null && vm.dataToConvertLead.closing_date.length > 0) ? vm.$options.filters.formatDate(vm.dataToConvertLead.closing_date) : '';
                                            fieldItem.value =new Date(Date.parse(fieldItem.value))
                                        }
                                        if (fieldItem.name == 'contract_name') {
                                            fieldItem.value = vm.accountName;
                                        }
                                        if (fieldItem.name == 'total_price') {
                                            fieldItem.value = (vm.dataToConvertLead.ammount != null && vm.dataToConvertLead.ammount.length > 0) ? vm.dataToConvertLead.ammount : '';
                                            var Tval = fieldItem.value;
                                            if (Tval == null)
                                                Tval = 0;
                                            Tval = Tval + "";
                                            if (Tval.includes(".")) {
                                                fieldItem.length = fieldItem.length + 3;
                                            }
                                        }
                                        if (fieldItem.picklist_options == "Lookup" && fieldItem.name == 'status_id') {
                                            fieldItem.value = 1001;
                                        }
                                    }
                                });
                            }
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    dealData: returnedData
                                };
                                vm.GroupBy.push(objGroup);
                            }
                        }
                    }, this));
                    vm.isConvertLeadToOthers = true;
                    vm.afterConvertToAccount = false;
                }
                setTimeout(function () {
                    $("div [name=account_id]").find(".vs__actions .clear").html("");
                }, 600);
                setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
                // setTimeout(function(){
                //     var frmFields={}
                //     vm.GroupBy.forEach((group) => {
                //         group.dealData.forEach((fieldItem) => {
                          
                //             frmFields[fieldItem.name]=fieldItem.value;
                //         })
                //     })
                //     // vm.$refs.ConvertLeadIntoDeal.resetForm({values:frmFields});
                // },2000);
            })
            $("#status_id option:selected").text("Select");
        },
        SubmitData: function (valid,errors) {
            var vm = this;
         
            var isDuplicate = false;
            var name = vm.projectDetail.ProjectName
            vm.submited = true;
            vm.Duplicate = 0;
          
            vm.$refs.ConvertLeadIntoDeal.validate().then((result) => {
                   
                if (result) {  
                    vm.isLoading= true;                  
                    vm.SaveDisabled = true;
                    var fullJson = vm.dealData;
                    var data = [];
                    var validation = [];
                    var selectedRadioValue = $("input[name='radio_clients']:checked").val();
                    var selectedClient = $.grep(vm.clientData, function (element, index) {
                        return element.user_id == selectedRadioValue;
                    });
                    if (selectedRadioValue) {
                         
                        if (name != '') {
                            var url ="name=" + name + "&type=PROJECT";
                            LeadDataService.CheckDuplicate(url).then(function (response) {
                            var json = response.data;
                                if (json != null) {
                                    vm.existName = name;
                                    vm.Duplicate = json;
                                    if (vm.Duplicate == 1) {
                                        $("input[name='ProjectName']").addClass('is-invalid');
                                        isDuplicate = true;
                                        vm.SaveDisabled = false;
                                         
                                    }
                                    if (!isDuplicate || vm.saveFor == 'DEAL') {
                                         
                                        $.each(fullJson, function (key, item) {
                                            var validationObject = {
                                                field_name: item.name,
                                                required: item.is_required,
                                                length: item.length,
                                                type: item.validation_type
                                            };
                                            var dataObject = {
                                                field_name: item.name,
                                                field_value: item.value,
                                                custom_field_id: item.custom_field_id
                                            };
                                            data.push(dataObject);
                                            validation.push(validationObject);
                                        });
                                        var postJSON = {
                                            data: data,
                                            validation: validation,
                                            clientData: selectedClient,
                                            ProjectDetail: vm.projectDetail
                                        };
                                        var objectPostString = JSON.stringify(postJSON);
                                        var url=vm.saveFor == 'DEAL' ? 'PostDeal' : 'PostContract';
                                            LeadDataService.PostDealOrContract(url,objectPostString).then(function (response) {
                                                vm.isLoading= false;
                                            if (response.data[0].Code == "1") {
                                                vm.$emit('close');                                               
                                                vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t((vm.saveFor == 'DEAL' ? 'Deal' : 'Contract'))]), "success", true, vm.$t("Alert"));
                                             if(vm.callFrom=='LeadListing'){
                                                $("[id^='ChangeOwnerId']").removeClass('enable').addClass('disabled');
                                                $("[id^='CloseLeadId']").removeClass('enable').addClass('disabled');
                                             }                                             
                                            }
                                            else {                                                
                                                vm.ShowAlert(vm.$t('AddedError',[vm.$t((vm.saveFor == 'DEAL' ? 'Deal' : 'Contract'))]), "failure", true, vm.$t("Alert"));
                                            }
                                            vm.SaveDisabled = false;
                                        });
                                    }
                                }
                            });
                        }
                        else {
                             
                            $.each(fullJson, function (key, item) {
                                if(item.name=='deal_owner_id' || item.name=='account_id') {item.value=item.value.value}
                                var validationObject = {
                                    field_name: item.name,
                                    required: item.is_required,
                                    length: item.length,
                                    type: item.validation_type
                                };
                                var dataObject = {
                                    field_name: item.name,
                                    field_value: item.value,
                                    custom_field_id: item.custom_field_id
                                };
                                data.push(dataObject);
                                validation.push(validationObject);
                            });
                            var postJSON = {
                                data: data,
                                validation: validation,
                                clientData: selectedClient
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            var url=vm.saveFor == 'DEAL' ? 'PostDeal' : 'PostContract';
                            LeadDataService.PostDealOrContract(url,objectPostString).then(function (response) {
                                    if (response.data[0].Code == "1") {
                                        vm.$emit('close');
                                        vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t((vm.saveFor == 'DEAL' ? 'Deal' : 'Contract'))]), "success", true, vm.$t("Alert"));
                                        vm.FetchData();
                                    }
                                    else {
                                        vm.ShowAlert(vm.$t('AddedError',[vm.$t((vm.saveFor == 'DEAL' ? 'Deal' : 'Contract'))]), "failure", true, vm.$t("Alert"));
                                    }
                                    vm.SaveDisabled = false;
                                });
                                vm.Duplicate = 0;
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t("Please Select Contact"), "warning", true, vm.$t("Alert"));
                            vm.SaveDisabled = false;
                             
                        }
                    }
                   else{
                    console.log(this.$refs.ConvertLeadIntoDeal.errors)
                   }
                });
            },
            MakeNormalArray: function (value) {
                if (value) {
                    try {
                        return JSON.parse(value);
                    }
                    catch (ex) {
                        return value;
                    }
                }
                else {
                    value = [];
                }
            },
        convertLead: function (ConversionType) {
            var contactIds = []
            var vueObj = this;
            var showMessage = '';
            vueObj.clientData = [];
        
            if (vueObj.createdAccountID > 0) {
                $.each(vueObj.leadConvertContactsData, function (filedIndex, fieldItem) {
                    var varObject = {
                        "name": fieldItem.first_name + ' ' + fieldItem.last_name,
                        "value": fieldItem.contact_id,
                        "first_value": null,
                        "second_value": null
                    }
                    contactIds.push(fieldItem.contact_id);
                    vueObj.contactListForAddDeal.push(varObject);
                });
            } else {
                $("#ConvertLeadContacts table tr td input[type='checkbox']:checked").each(function () {
                    var checkboxObj = this
                    contactIds.push($(checkboxObj).val());
                    var filteredObj = [];
                    filteredObj = $.grep(vueObj.leadConvertContactsData, function (n, i) {
                        return n.contact_id == $(checkboxObj).val();
                    });
                    if (filteredObj.length > 0) {
                        var varObject = {
                            "name": filteredObj[0].first_name + ' ' + filteredObj[0].last_name,
                            "value": filteredObj[0].contact_id,
                            "first_value": null,
                            "second_value": null
                        }
                        vueObj.contactListForAddDeal.push(varObject);
                    }
                });
                showMessage = vueObj.$t('LeadConvertSuccess')+ vueObj.$t('Lead')+vueObj.$t('Account');
            }
            if (vueObj.contactId == 0) {
                vueObj.ShowAlert("Please add contact first.", "warning", true,vueObj.$t('Alert'))
                return false;
            }
            var txtAccountName = $.trim(vueObj.IsActiveAccount == 1 ? vueObj.NewAccName : vueObj.AccName);
            if (txtAccountName == "") {
                vueObj.ShowAlert("Please fill Account Name.", "warning", true,vueObj.$t('Alert'));
                return false;
            }
            if (contactIds.length < 1) {
                contactIds.push(vueObj.contactId);
                var filteredObj = [];
                filteredObj = $.grep(vueObj.leadConvertContactsData, function (n, i) {
                    return n.contact_id == vueObj.contactId;
                });
                if (filteredObj.length > 0) {
                    var varObject = {
                        "name": filteredObj[0].first_name + ' ' + filteredObj[0].last_name,
                        "value": filteredObj[0].contact_id,
                        "first_value": null,
                        "second_value": null
                    }
                    vueObj.contactListForAddDeal.push(varObject);
                }
            }
            if (contactIds.length > 0) {
                 
                vueObj.accountName = txtAccountName;
                var ConvertLeadData = {
                    sourceId: vueObj.leadId,
                    moduleName:vueObj.moduleName,
                    subModuleCode: vueObj.subModuleCode,
                    accountName: vueObj.accountName,
                    contactIds: contactIds,
                    accountId: vueObj.IsActiveAccount == 1 ? 0 : ($.trim(vueObj.createdAccountID) == "" ? 0 : vueObj.createdAccountID),
                    company: vueObj.LeadCompany,
                    address1: vueObj.Leadaddr1,
                    address2: vueObj.Leadaddr2,
                    city: vueObj.Leadcity,
                    zip: vueObj.Leadzip,
                    state: vueObj.Leadstate
                };
                var objectPostString = JSON.stringify(ConvertLeadData);
                LeadDataService.ConvertLead(objectPostString).then(function (response) {
                    if (response.data[0].Status == "Success") {
                        var json = vueObj.MakeNormalArray(response.data[0].Value);
                        vueObj.successMessage = showMessage;
                      vueObj.isConvertLeadToOthers = true
                        vueObj.createdAccountID = json.ACCOUNT;
                        vueObj.clientData = json.CLIENTS;
                        vueObj.getClientData(json.ACCOUNT);
                        if (ConversionType == 'DEAL')
                            setTimeout(function () {
                                vueObj.FetchData(json.ACCOUNT, 'DEAL');
                            }, 300);
                        else if (ConversionType == 'CONTRACT')
                            setTimeout(function () {
                                vueObj.FetchData(json.ACCOUNT, 'CONTRACT');
                            }, 300);
                    }
                     
                });
            } else {
                if (vueObj.createdAccountID > 0) {
                    var accountName = vueObj.FilterDDLandNormalValue(vueObj.dataToConvertLead.account_id);
                    vueObj.isContactForExistsAccount = false;vm.$t("SubmitSuccess")
                    vueObj.successMessage = vueObj.$t("AccountIsNotAssciated")+ accountName;
                } else {
                    vueObj.isErrorMessage = vueObj.$t("PleaseSelectAnyRecordsForFurther");
                    vueObj.isSelected = true;
                }
            }            
        },
        closePopup: function () {
            var vm = this;
            var AccountIdData = {
                accntid: vm.createdAccountID
            };
            var objectPostString = JSON.stringify(AccountIdData);
           LeadDataService.DeleteAccountBeforeDeal(objectPostString).then(function (response) {
            console.log(response);
            });
            if (vm.isConvertLeadToOthers) {
                vm.isConvertLeadToOthers = false;
                vm.$emit('close');
            }
            else {
                vm.$emit('close');
            }
        },
        GoPrevious: function () {
            var vm = this;
            var AccountIdData = {
                accntid: vm.createdAccountID
            };
            var objectPostString = JSON.stringify(AccountIdData);
            LeadDataService.DeleteAccountBeforeDeal(objectPostString).then(function (response) {
                console.log(response);
            });
            vm.isConvertLeadToOthers = false;
            vm.IsActiveAccount = 1;
            vm.showConvertLead = true;
            $("div [name=account_id]").find(".vs__actions .clear").html("");
        },
        GetValidation: function (object) {
            var obj = {};
            if (object.is_required) {
                obj['required'] = true;
            }
            if (object.length > 0) {
                obj['max'] = object.length;
            }
            if (object.validation_type == "email") {
                obj['email'] = true;
            }
          
            if (object.dt_code == "decimal" || object.dt_code == "float") {
                //obj['decimal'] = 2;
                if (object.name == "ORIGNAL_CONVERSION_RATE") {
                    obj['min_value'] = 0;
                }
                if (object.name == "estimated_hours" || object.name == "estimated_cost" || object.name == "base_rate") {
                    obj['min_value'] = 1;
                }
                if ( object.name == "base_rate") {
                    obj['max_value'] = 9999.99;
                }
            }
            if (object.dt_code == "int" || object.dt_code == "bigint") {
                obj['numeric'] = true;
            }
            if (object.dt_code == "url") {
                obj['url'] = 'require_protocol';
            }
            if (object.regular_expression != null && object.regular_expression != "") {
                obj['regex'] = object.regular_expression;
            }
            if (object.name == "project_named" && object.value != "") {
                var regexp = /^[A-Za-z0-9 _]*[A-Za-z0-9][A-Za-z0-9 _]*$/;
                obj['regex'] = regexp;
            }
            return obj;
        },
    }
}

</script>


<template>
 <div class="modal d-block my-popups dialog">
            <loader :is-visible="isLoading"></loader>
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <slot name="headerForModal">
                            <h5 class="modal-title">{{popupHeader}}</h5>
                        </slot>
                        <button type="button" title="" v-on:click="$emit('close');closePopup();" class="close"
                            data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                    </div>
                    <div class="modal-body">
                        <Form ref="ConvertLeadIntoDeal" tag="form" v-slot="{valid,errors}">
                        <div id="divConvertLeadIntoAccount"
                            v-if="!afterConvertToAccount && !isConvertLeadToOthers"
                            v-bind:class="{'fade':(isConvertLeadToOthers)}">
                            
                                <div class="row">
                                    <div class="col-md-12">

                                        <div class="card-body p-0">
                                            <div class="panel1"  v-for="(group,index) in LeftViewData" :key="index">
                                                <div class="heading-border">
                                                    <h5 class="h5 mb-2">
                                                        <span>{{$t(group.group_name,'')}}</span>
                                                    </h5>
                                                </div>
                                                <div class="row">
                                                    <div class="col-md-6" v-for="(item, index1) in group.LeftViewData" :key="index1">
                                                        <div class="form-group">
                                                            <label>
                                                                <strong>{{$t(item.name,
                                                                    item.display_name)}}:</strong>
                                                                <span class="answer rightlabeltext ml-1"
                                                                    v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">{{$options.filters.formatDate(item.value)}}</span>
                                                                <span class="answer rightlabeltext ml-1"
                                                                    v-if="item.dt_code == 'decimal'">{{isBlank(item.value)?"0.00":$options.filters.currency(item.value) }}</span>
                                                                <span class="answer rightlabeltext ml-1"
                                                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'">{{
                                                                    isBlank(item.value)?"--":(item.value)}}</span>
                                                                <span class="answer rightlabeltext ml-1"
                                                                    v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{
                                                                    isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}</span>
                                                            </label>
                                                        </div>
                                                    </div>
                                                    <div class="col-md-6">
                                                        <label>
                                                            <strong>Company Name:</strong>
                                                            <span class="answer rightlabeltext"
                                                                v-html="LeadCompany"></span>
                                                        </label>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                           
                            <div class="heading-border mt-2">
                                <h5 class="h5"><span>New Account</span> </h5>
                            </div>
                            <div class="col-md-12 p-0">
                                <div class="form-group row mt-2">
                                    <div class="col-md-6">
                                        <label><input type="radio" v-bind:disabled="LeadStatus == 'InProgress'"
                                                name="Account" v-on:click="ChangeAccount(IsActiveAccount)"
                                                v-model="IsActiveAccount" value="1" /> Create New:</label>
                                        <input type="text" class="form-control" v-bind:readonly="IsActiveAccount==2"
                                            v-model="NewAccName" v-bind:name="NewAccName" />
                                    </div>
                                    <div class="col-md-6">
                                        <label><input type="radio" name="Account" v-model="IsActiveAccount"
                                                v-on:click="ChangeAccount(IsActiveAccount)" value="2" /> Select
                                            Account:</label>

                                        <select v-model="OldAccountId" class="form-control"
                                            v-bind:readonly="IsActiveAccount==1"
                                            v-on:change="getName(ACCountDDL,OldAccountId)">
                                            <option v-for="(option,index2) in ACCountDDL" v-bind:value="option.value" :key="index2">
                                                {{option.name}}</option>
                                        </select>
                                    </div>
                                </div>
                            </div>

                        </div>
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2 p-0" v-if="afterConvertToAccount">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2"
                                v-if="successMessage!='' && isContactForExistsAccount">
                                <div class="alert alert-success alert-dismissible mt-1">
                                    <a href="javascript:;" class="close" data-dismiss="alert"
                                        aria-label="close">&times;</a>
                                    {{successMessage}}
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2"
                                v-if="!isContactForExistsAccount">
                                <div class="alert alert-danger alert-dismissible mt-1">
                                    <a href="javascript:;" class="close" data-dismiss="alert"
                                        aria-label="close">&times;</a>
                                    {{successMessage}}
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2 text-center"
                                v-if="isContactForExistsAccount">
                                <a href="javascript:;"
                                    v-on:click="popupHeader = '@Resources.AddContract';FetchData(createdAccountID,'CONTRACT')"
                                    class="btn btn-success" v-if="!isConvertLeadToOthers"><em class="fa fa-plus"></em>{{$t('AddContract')}}</a>
                                <a href="javascript:;"
                                    v-on:click="popupHeader = '@Resources.Add @Resources.Deal';FetchData(createdAccountID, 'DEAL')"
                                    class="btn btn-success" v-if="!isConvertLeadToOthers"><em class="fa fa-plus"></em>{{$t('Add')}} {{$t('Deal')}}</a>
                            </div>
                        </div>
                        <div id="divConvertLeadIntoDealAfterAccount" v-if="isConvertLeadToOthers">
                            <div id="divMainDealData" class="d-flex flex-wrap">                              
                               
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mt-2 validationfixer p-0"
                                        v-for="(group,parentIndex) in GroupBy" :key="parentIndex">
                                        <div class="passpolicy-detailshead mb-3">
                                            <div class="heading-border">
                                                <h5 class="h5">
                                                    <span>{{$t(group.group_name,group.group_display_name)}}</span>
                                                </h5>
                                            </div>
                                        </div>
                                        <div class="d-flex flex-wrap row">
                                            <div v-bind:class="{'col-sm-12 col-md-12 float-left': true, 'd-none' : item.form_field_visibility == 'NO' ||
                                             item.name == 'contact_name' || item.name == 'mobile_phone' || item.name =='home_phone' || 
