<template>
    <transition name="modal"> 
        <div class="my-popups">
            <loader :is-visible="isLoading"></loader>
           <div class="modal d-block">
               <div class="modal-dialog modal-dialog-centered">
                  <div class="modal-content">
                      <div class="modal-header">
                            <h5 class="modal-title">
                          {{$t('LEAD_NAME')}}: {{leadName}}
                           </h5>
                       <button  v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times" aria-hidden="true"></em></button>
                   </div>
                      <div class="modal-body">
                   <div class="panel1" v-for="(group,index) in LeftViewData" :key="index">
                            <div class="heading-border">
                                <h5 class="h5 mb-2"><span>{{ $t(group.group_name.toUpperCase(),'')}}</span></h5>
                         </div>
                           <div class="row">
                           <div class="col-md-6 col-lg-3" v-for="(item, index1) in group.LeftViewData" :key="index1">
                            <div class="form-group">
                                     <label>
                                      <strong>{{ $t(item.name.toUpperCase(), item.display_name)}}:</strong>
                                      <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'date' || item.dt_code =='datetime'">
                                    {{ item.value | formatDate }}
                                    </span>
                                           <span class="answer rightlabeltext ml-1" v-if="item.dt_code == 'decimal'">
                                            {{isBlank(item.value)?"0.00":item.value | currency}}
                                                     </span> 
                                         <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime' && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'">
                                            {{ isBlank(item.value)?"--":(item.value)}}
                                             </span> 
                                             <span class="answer rightlabeltext ml-1" v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">
                                                 {{ isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}
                                             </span> 
                                         </label> 
                                     </div>
                                 </div>
                         </div> 
                       </div>
                      </div> 
                   </div> 
               </div>
           </div> 
        </div>
        </transition>
</template>
<script>
import LeadDataService from "../../../services/DataService";
export default {
     props: {        
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        sourceIds:{
            type: Number,
            required: true
        },
        controller: {
            type: String,
            required: true
        },
       
    },
    data: function () {
        return {
            LeftViewData: [],
            sourceId: null,          
            ApiURL: "moduleName="+this.modulename+"&subModuleCode="+this.submodulecode,
            leadName: '--',
            itemOfListingScreen: null,
            isLoading:false
            
        }
    },
 
    created: function () {
        console.log('source '+this.sourceIds);
        this.sourceId =this.sourceIds;
        this.itemOfListingScreen = this.$parent.LeadIdForViewNote;
        this.FetchLeadDetails();
    },
    methods: {
        FetchLeadDetails: function () {
        var vm = this;
        vm.isLoading=true;
            vm.ApiURL = vm.ApiURL + "&id=" + vm.sourceId;
           LeadDataService.getQuickViewDetails(vm.ApiURL)
        .then(function (response) {
            vm.isLoading=false;
               var fullLeftJSON = response.data.LEFTDATA;
                var clienFirstName = "";
                var clientLastName = "";
                fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                    if (element.name == 'client_first_name') {
                        clienFirstName = (element.value != null) ? element.value : '';
                    }
                    else if (element.name == 'client_last_name') {
                        clientLastName = (element.value != null) ? element.value : '';
                    }
                    return (element.form_field_visibility == 'YES');
                });
                if (vm.leadName == '--') {
                    vm.leadName = clienFirstName + ' ' + clientLastName;
                }
                //for main data
                if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                    var groupId = fullLeftJSON[0].group_id;

                    $.each(fullLeftJSON, $.proxy(function (index, item) {

                        var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    LeftViewData: returnedData
                                };

                                vm.LeftViewData.push(objGroup);

                            }
                        }
                        groupId = item.group_id;
                    }, this));
                }
                //console.log(this.LeftViewData);
                setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
                setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
                //setTimeout("SetDates()", 100);
            });
        },
        isBlank: function (str) {
                return (!str || /^\s*$/.test(str));
            },
             FilterDDLandNormalValue: function (item) {
                if (item.length > 0) {
                    try {
                        return JSON.parse(item)[0].name;
                    } catch (ex) { return '' }
                } else {
                    return '';
                }
            },
}
}
</script>