<script>
import LeadDataService from "../../../services/DataService";
export default {
  props: {
   
    subModuleCode:null,
    
   },
  data: function () {
    return {
      leadOwner: [],
      leadOwnerList: [],
      leadOwnerListAll: [],
      permissions: [],
      chkpermission: false,
      additionalLeadOwnerIdForEdit: null,
      vSelectValidate: false     
    };
  },
  created: function () {
   
    this.additionalLeadOwnerIdForEdit = this.$parent.additionalLeadOwnerId;
    this.getLeadOwnerList();
    this.GetAllLeadOwnerPermission();   
  },
  methods: {    
    getLeadOwnerList: function () {
      var vm = this;
      var url = "id=&moduleName=CRM&type=CRM_USERS";       
      LeadDataService.getDDLData(url).then(function (response) {
        var json = response.data;
        if (json != null) {
          vm.leadOwnerList = json.DATA;
          vm.leadOwnerList = vm.leadOwnerList.map((item) => {
        return { value: item.value, label: item.name };
      });
          vm.leadOwnerListAll = json.DATA;
          if (vm.additionalLeadOwnerIdForEdit > 0) {
            var defaultUser = vm.$parent.additionalLeadOwners.filter(function (
              x
            ) {
              return x.IS_DEFAULT == 1;
            });
            $.each(defaultUser, function (index, item) {
              vm.leadOwnerList = vm.leadOwnerList.filter(function (x) {
                return x.value != item.LEAD_OWNER_ID;
              });
            });
            vm.leadOwner = vm.leadOwnerList.filter(function (x) {
              return x.value == vm.additionalLeadOwnerIdForEdit;
            });
      //       vm.leadOwner=vm.leadOwner.map((item) => {
      //   return item.value
      // });
            vm.leadOwnerListAll = vm.leadOwnerList;
          } else {
            $.each(vm.$parent.additionalLeadOwners, function (index, item) {
              vm.leadOwnerList = vm.leadOwnerList.filter(function (x) {
                return x.value != item.LEAD_OWNER_ID;
              });
            });
            vm.leadOwnerListAll = vm.leadOwnerList;
          }
        }
         
      });
    },
    clickOnView: function (item) {
      var vueObj = this;
      if (item.View[0].isSelected) {
        vueObj.chkpermission = false;
        if (item.Add.length > 0) {
          item.Add[0].isDisable = false;
        }
        if (item.Update.length > 0) {
          item.Update[0].isDisable = false;
        }
        if (item.Delete.length > 0) {
          item.Delete[0].isDisable = false;
        }
      } else {
        if (item.Add.length > 0) {
          item.Add[0].isDisable = true;
          item.Add[0].isSelected = false;
        }
        if (item.Update.length > 0) {
          item.Update[0].isDisable = true;
          item.Update[0].isSelected = false;
        }
        if (item.Delete.length > 0) {
          item.Delete[0].isDisable = true;
          item.Delete[0].isSelected = false;
        }
      }
    },
    clickOnAdd: function (item) {
      var vueObj = this;
      if (item.Add[0].isSelected) {
        vueObj.chkpermission = false;
      }
    },
    clickOnUpdate: function (item) {
      var vueObj = this;
      if (item.Update[0].isSelected) {
        vueObj.chkpermission = false;
      }
    },
    clickOnDelete: function (item) {
      var vueObj = this;
      if (item.Delete[0].isSelected) {
        vueObj.chkpermission = false;
      }
    },
    GetAllLeadOwnerPermission: function () {
      var vm = this;
      var leadOwnerId =
        this.additionalLeadOwnerIdForEdit != null
          ? this.additionalLeadOwnerIdForEdit
          : null;
      var url =vm.subModuleCode=='CRM_DEALS'? `subModuleCode=CRM_DEALS&leadOwner=${leadOwnerId}&leadId=${(vm.$route.path.indexOf('Listing')>-1||vm.$route.path.indexOf('Index')>-1)?null:vm.$route.params.id}`:
      `subModuleCode=CRM_LEADS&leadOwner=${leadOwnerId}&leadId=${(vm.$route.path.indexOf('Listing')>-1||vm.$route.path.indexOf('Index')>-1)?null:vm.$route.params.id}`;
      url = url.replaceAll("null", "");       
      LeadDataService.GetAllLeadOwnerPermission(url).then(function (response) {      
        if (response.data != null) {
        var json = response.data.permission;
          var assignedPermission = response.data.assignedPermission;
          $.each(json, function (index, item) {
            var ifalreadyExist = $.grep(
              vm.permissions,
              function (element, key) {
                return element.crmModuleId == item.crm_module_id;
              }
            );
            if (ifalreadyExist.length == 0) {
              ifalreadyExist = $.grep(json, function (element, key) {
                return element.crm_module_id == item.crm_module_id;
              });
              if (ifalreadyExist.length > 0) {
                var permissionObj = [];
                var checkForView = ifalreadyExist.filter(function (x) {
                  return x.permission_name == "View";
                });
                $.each(ifalreadyExist, function (pindex, pObj) {
                  
                  var ifAlreadyAssigned = [];
                  if (assignedPermission.length > 0) {
                    ifAlreadyAssigned = assignedPermission.filter(function (x) {
                      return x.permission_id == pObj.permission_id;
                    });
                  }
                  var obj = {
                    permission_name: pObj.permission_name,
                    permission_id: pObj.permission_id,
                    isSelected: ifAlreadyAssigned.length > 0 ? true : false,
                    isDisable:
                      pObj.permission_name == "View" || checkForView.length == 0
                        ? false
                        : true,
                  };
                  permissionObj.push(obj);
                });
                var obj = {
                  crmModuleId: item.crm_module_id,
                  crmModuleName: item.module_name,
                  View: permissionObj.filter(function (x) {
                    return x.permission_name == "View";
                  }),
                  Add: permissionObj.filter(function (x) {
                    return x.permission_name == "Add";
                  }),
                  Update: permissionObj.filter(function (x) {
                    return x.permission_name == "Update";
                  }),
                  Delete: permissionObj.filter(function (x) {
                    return x.permission_name == "Delete";
                  }),
                };
                vm.permissions.push(obj);
              }
            }
          });
        }
         
      });
    },
    saveAdditionalOwnerPermission: function () {     
      var vueObj = this;
    var leadOwnerIds = "";
      vueObj.chkpermission = true;
   vueObj.$refs.form.validate().then((result) => {       
        if (result) {
          if (vueObj.leadOwner.length > 0) {
            $.each(vueObj.leadOwner, function (index, item) {
              leadOwnerIds =
                leadOwnerIds +
                (leadOwnerIds == "" ? item.value : "," + item.value);
            });
            var resultval = "";
            $(".chkItems:checkbox:checked").each(function () {
              if (resultval.length > 0) resultval += ",";
              resultval += $(this).val();
            });
            var permissions = {
              leadOwner: leadOwnerIds,
              leadId: resultval==""?vueObj.$route.params.id:resultval,
              permissionIds: "",
            };
            var permnIds = "";
            $.each(vueObj.permissions, function (index, item) {
              if (item.View.length > 0 && item.View[0].isSelected) {
                permnIds =
                  permnIds +
                  (permnIds == ""
                    ? item.View[0].permission_id
                    : "," + item.View[0].permission_id);
              }
              if (item.Add.length > 0 && item.Add[0].isSelected) {
                permnIds =
                  permnIds +
                  (permnIds == ""
                    ? item.Add[0].permission_id
                    : "," + item.Add[0].permission_id);
              }
              if (item.Update.length > 0 && item.Update[0].isSelected) {
                permnIds =
                  permnIds +
                  (permnIds == ""
                    ? item.Update[0].permission_id
                    : "," + item.Update[0].permission_id);
              }
              if (item.Delete.length > 0 && item.Delete[0].isSelected) {
                permnIds =
                  permnIds +
                  (permnIds == ""
                    ? item.Delete[0].permission_id
                    : "," + item.Delete[0].permission_id);
              }
            });
            permissions.permissionIds = permnIds;
            var objectPostString = JSON.stringify(permissions);
            if(vueObj.subModuleCode=='CRM_DEALS' || window.location.pathname.indexOf('DealListing')>-1){
              LeadDataService.saveAdditionalDealOwnerPermission(
              objectPostString
            ).then(function (response) {      
                     
              if (response != null && response.data[0].Status == "Success") {
                vueObj.ShowAlert(vueObj.$t("RecordsSavedSuccessfully"));
                vueObj.$parent.showAdditionalOwnerPopup = false;
                $(".chkItems:checkbox:checked")
                  .prop("checked", false)
                  .removeAttr("checked");
                $("#DeleteMultiple").addClass("disabled");
                if(vueObj.$route.params.id>0){
                  vueObj.$parent.loadData(vueObj.subModuleCode=='CRM_DEALS'?'additionalDealowners': 'additionalleadowners');
                }
                vueObj.$emit("close");
              }
            });
            }else{
            LeadDataService.saveAdditionalOwnerPermission(
              objectPostString
            ).then(function (response) {             
              if (response != null && response.data[0].Status == "Success") {
                vueObj.ShowAlert(vueObj.$t("RecordsSavedSuccessfully"));
                vueObj.$parent.showAdditionalOwnerPopup = false;
                $(".chkItems:checkbox:checked")
                  .prop("checked", false)
                  .removeAttr("checked");
                $("#DeleteMultiple").addClass("disabled");
                if(vueObj.$route.params.id>0){
                  vueObj.$parent.loadData('additionalleadowners');
                }
                vueObj.$emit("close");
              }
            });
          }
          }
        }
      });
    },
  },
};
</script>

<template>
  <transition name="modal">
    <div class="modal d-block my-popups dialog">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <slot name="headerForModal">
              <h5 class="modal-title">{{$t("AdditionalOwner")}}</h5>
            </slot>
            <button
              type="button"
              title
              v-on:click="$emit('close')"
              class="close"
              data-original-title="Close"
            >
              <em class="fa fa-times" aria-hidden="true"></em>
            </button>
          </div>
          <div class="modal-body">
            <Form ref="form">
              <div class="d-flex flex-wrap">
                <div class="col-md-12 p-0">
                  <div class="row">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                      <div class="form-group">
                        <label v-if="subModuleCode=='CRM_DEALS'"> {{$t("DealOwner")}}: <span class="text-danger">*</span> </label> 
                        <label  v-else> {{$t("LeadOwner")}}: <span class="text-danger">*</span> </label>                       
                        <Field name="name" :rules="{'required':true}" label="name" v-slot="{errors,changed}">
                          <Multiselect
                            :object="true"
                            label="label"
                            :show-labels="false"
                              v-model="leadOwner"                           
                              :multiple="true" 
                              :taggable="true"
                              track-by="value"
                              :placeholder="subModuleCode=='CRM_DEALS'?'Deal Owner Search':'Lead Owner Search'"                                                
                              :searchable="true"                            
                              @update:modelValue="changed"
                              :options="leadOwnerList">
                            </Multiselect>                          
                          <span name="name" class="invalid-feedback d-block" >{{errors[0]}}</span>                         
                        </Field>                   
                      </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3 float-left listing">
                      <div class="table-responsive">
                        <table class="table table-bordered mb-0" aria-describedby="addaddtionalowner">
                          <thead class="thead-bg">
                            <tr>
                               <th scope="col">
                                <span>{{$t("Permission")}} {{$t("For")}}</span>
                              </th>
                               <th scope="col">
                                <span>{{$t("View")}}</span>
                              </th>
                               <th scope="col">
                                <span>{{$t("Add")}}</span>
                              </th>
                               <th scope="col">
                                <span>{{$t("Update")}}</span>
                              </th>
                               <th scope="col">
                                <span>{{$t("Delete")}}</span>
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-for="item in permissions" :Key="item">
                              <td data-id="item.crmModuleId">{{item.crmModuleName}}</td>
                              <td>                              
                                <label class="switch" v-if="item.View.length > 0">                                                                 
                                  <input
                                    class="form-check-input rdoticketfor custom-control-input dynamic"
                                    type="checkbox"
                                    v-model="item.View[0].isSelected"
                                    v-bind:value="item.View[0].permission_id"
                                    v-if="item.View.length > 0"
                                    v-bind:disabled="item.View[0].isDisable"
                                    v-bind:onchange="clickOnView(item)"
                                    v-bind:checked="item.View[0].isSelected == 1"
                                  />                                     
                                  <span class="slider round">
                                    <span class="slider-yes">{{$t("Yes")}}</span>
                                    <span class="slider-no">{{$t("No")}}</span>
                                  </span>
                                </label>
                              </td>
                              <td>
                                <label class="switch" v-if="item.Add.length > 0">
                                  <input
                                    class="form-check-input rdoticketfor custom-control-input dynamic"
                                    type="checkbox"
                                    v-model="item.Add[0].isSelected"
                                    v-bind:value="item.Add[0].permission_id"
                                    v-if="item.Add.length > 0"
                                    v-bind:disabled="item.Add[0].isDisable"
                                    v-bind:onchange="clickOnAdd(item)"
                                    v-bind:checked="item.Add[0].isSelected == 1"
                                  />
                                  <span class="slider round">
                                    <span class="slider-yes">{{$t("Yes")}}</span>
                                    <span class="slider-no">{{$t("No")}}</span>
                                  </span>
                                </label>
                              </td>
                              <td>
                                <label class="switch" v-if="item.Update.length > 0">
                                  <input
                                    class="form-check-input rdoticketfor custom-control-input dynamic"
                                    type="checkbox"
                                    v-model="item.Update[0].isSelected"
                                    v-bind:value="item.Update[0].permission_id"
                                    v-if="item.Update.length > 0"
                                    v-bind:disabled="item.Update[0].isDisable"
                                    v-bind:onchange="clickOnUpdate(item)"
                                    v-bind:checked="item.Update[0].isSelected == 1"
                                  />
                                  <span class="slider round">
                                    <span class="slider-yes">{{$t("Yes")}}</span>
                                    <span class="slider-no">{{$t("No")}}</span>
                                  </span>
                                </label>
                              </td>
                              <td>
                                <label class="switch" v-if="item.Delete.length > 0">
                                  <input
                                    class="form-check-input rdoticketfor custom-control-input dynamic"
                                    type="checkbox"
                                    v-model="item.Delete[0].isSelected"
                                    v-bind:value="item.Delete[0].permission_id"
                                    v-if="item.Delete.length > 0"
                                    v-bind:disabled="item.Delete[0].isDisable"
                                    v-bind:onchange="clickOnDelete(item)"
                                    v-bind:checked="item.Delete[0].isSelected == 1"
                                  />
                                  <span class="slider round">
                                    <span class="slider-yes">{{$t("Yes")}}</span>
                                    <span class="slider-no">{{$t("No")}}</span>
                                  </span>
                                </label>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                      
                    </div>
                  </div>
                  <div class="row flex-row-reverse">
                    <div class="col-lg-6 text-right">
                      <a
                        href="javascript:;"
                        class="btn btn-success save-btn mr-1"
                        title
                        v-on:click="saveAdditionalOwnerPermission()"
                      >
                        <em  class="fa fa-save pr-2"></em>
                        {{$t("Save")}}
                      </a>
                      <a
                        href="javascript:;"
                        v-on:click="$emit('close')"
                        class="btn btn-danger delete-btn"
                      >
                        <em  class="fa fa-times"></em>
                        {{$t("Cancel")}}
                      </a>
                    </div>
                    <div class="col-lg-6">
                      <div v-if="chkpermission" class="py-2"><small class="text-danger">{{$t("requiredpermission")}}</small></div> 
                    </div>
                  </div>
                </div>
              </div>
            </Form>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>