<template>
  <transition name="modal">
    <div class="my-popups">
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title">{{header}}</h5>
              <span class="float-right">
                <a
                  href="javascript:;"
                  class="text-white"
                  data-toggle-tooltip="tooltip"
                  data-original-title="Card View"
                  v-on:click="NoteListType = 'card'"
                  v-if="NoteListType == 'list'"
                >
                  <em class="fa fa-th-large mr-2"></em>{{$t('CardView')}}
                </a>
                <a
                  href="javascript:;"
                  class="text-white"
                  data-toggle-tooltip="tooltip"
                  data-original-title="List View"
                  v-on:click="NoteListType = 'list'"
                  v-if="NoteListType != 'list'"
                >
                  <em class="fa fa-list mr-2"></em>{{$t('ListView')}}
                </a>
              </span>
              <button v-on:click="$emit('close')" href="javascript:;" class="close">
                <em class="fa fa-times" aria-hidden="true"></em>
              </button>
            </div>
            <div class="modal-body" style="max-height:600px;overflow-y:auto;">
              <div class>
                <crmnote
                  v-bind:notedata="noteData"
                  v-bind:sourceid="allIds"
                  v-bind:modulename="modulename"
                  v-bind:submodulecode="submodulecode"
                  callfrom="quickViewAndNoteJS"
                  v-bind:userid="userid"
                  v-bind:curIndex="curindex"
                  v-bind:listtype="NoteListType"
                  :showNotePopUp="isShowNotePopup"
                ></crmnote>
              </div>
            </div>
            <div class="modal-footer AddNote" v-if="!isShowNotePopup">
              <div class="col-lg-6 text-right p-0">
                <a
                  href="javascript:;"
                  class="btn btn-success formbtn text-white"
                  v-on:click="openShowNotePopup()"
                >
                 <em class="fa fa-plus pr-2"></em>{{$t('AddNote')}}
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import crmnote from '../Modal/crmnote.vue'
import LeadDataService from "../../../services/DataService";
export default {
  props: {
    modulename: {
      type: String,
      required: true,
    },
    submodulecode: {
      type: String,
      required: true,
    },
    userid: {
      type: Number,
      required: true,
    },
    sourcekeyname: {
      type: String,
      required: false,
    },
    curindex: {
      type: Number,
    },
    leadInfo:{
    type: Object,
      required: true,
    },
    DealInfo:{
    type: Object,
      required: false,
    }
  },
  components:{
crmnote
  },
  data: function () {
    return {
      LeadId: null,
      DealId: null,
      allIds: null,
      noteData: [],
      noteslead: null,
      crm_NotesId: null,
      NoRecordForTabNotes: true,
      isShowLeadNoteDialogue: false,
      editDeleteButonFor: 0,
      header: "",
      isShowNotePopup: false,
      NoteListType: "list",
    };
  },
  watch: {
    LeadId: function (value) {
      this.allIds = value;
      this.loadNoteData(value);
    },
    DealId: function (value) {
      this.allIds = value;
      this.loadNoteData(value);
    }   
  },
  created: function () {
  console.log(this.userid)
    try {
      this.LeadId = this.leadInfo.lead_id;
    } catch (e) {}
    try {
      console.log(this.DealInfo.deal_id)
      this.DealId = this.DealInfo.deal_id;
    } catch (e) {}
    this.header = this.$t("Notes");
    if (this.LeadId != null) {
      if (this.submodulecode == "CRM_LEADS")
       
        this.NoteListType = "list";
      //
      this.allIds = this.leadInfo.lead_id;
      this.loadNoteData(this.LeadId);
    } else if (this.DealId != null) {
      
      this.allIds = this.DealInfo.deal_id;
      if (this.submodulecode == "CRM_DEALS")
        
        this.NoteListType = "list";
      this.loadNoteData(this.DealId);
    }
  },
  methods: {
    openShowNotePopup: function () {
      var ths = this;
      if (this.submodulecode == "CRM_LEADS") {
        if (
          this.leadInfo != null &&
          this.leadInfo.is_additional_lead_owner != "NO"
        ) {
          var json = JSON.parse(
            vm.leadInfo.is_additional_lead_owner
          );
          var prmsn = json.filter(function (c) {
            return c.permission_code == "AddNote";
          });
          if (prmsn.length > 0) {
            ths.isShowNotePopup = true;
            ths.loadNoteData(ths.LeadId);
          } else {
            ths.ShowAlert(
             this.$t("additionalOwnerAuthorityWarning"),
              "warning",
              true,
              this.$t("Alert")
            );
          }
        } else {
     
          ths.isShowNotePopup = true;
          ths.loadNoteData(ths.LeadId);
        }
      } else {
     
        ths.isShowNotePopup = true;
        if (ths.LeadId != null) {
          ths.loadNoteData(ths.LeadId);
        } else {
          ths.loadNoteData(ths.DealId);
        }
      }
    },
    loadNoteData: function (id) {
      var vm = this;
      var leadId = id;
       var url =
        "id=" +
        leadId +
        "&subModuleCode=" +
        vm.submodulecode +
        "&moduleName=" +
        vm.modulename;
      
    LeadDataService.NoteListing(url) .then(function (response) {
           
          if (response.data != null && response.data.data.length > 0) {            
            vm.noteData = response.data.data;
            if (vm.noteData != null && vm.noteData.length > 0) {
              var tmpData = vm.noteData.filter(function (obj) {
                return obj.created_by == vm.userid;
              });
              if (tmpData.length > 0) {
                vm.editDeleteButonFor = tmpData[0].crm_note_id;
              }
            }
            setTimeout(function(){vm.CheckBoxBootstrap()}, 100);
            vm.NoRecordForTabNotes = false;
          } else {
            vm.NoRecordForTabNotes = true;
            vm.noteData = [];
          }
        });
    },
  },
};
</script>