<script>
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
export default {
    name:'crm-readmore',
     props: {
        data: {
            type: String,
            required: true
        },
        heading: {
            type: String,
            required: true
        },
        length: {
            type: String,

        },
        callFrom:null
    },
    data: function () {
        return {
            isShowFullData: false,
            tempData:'',
            editor: ClassicEditor,
        }
    },
    methods: {
      onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        openFullDateModal: function () {
            this.isShowFullData = true;
        },
        returnShortNoteString: function () {
            var el = document.implementation.createHTMLDocument().createElement('div');
            if(this.callFrom=='Notes'){
                if (this.data.length > 0) {                    
                    // this.data = this.data.replace('<', '&lt;').replace('>', '&gt;');
                }     
            }
                 
            el.innerHTML = this.data;
            var tmpData = el.innerText;
            this.tempData = tmpData;
            if (tmpData.length <= this.length) return this.data;
            return tmpData.substring(0, this.length);
        },
        isShowReadMoreButtton: function () {
            var el = document.implementation.createHTMLDocument().createElement('div');
            el.innerHTML = this.data;
            var tmpData = el.innerText;
            if (tmpData.length <= this.length) return false;
            return true;
        }
    }
}
</script>
<template>
    <div style="display: inline;">
        <span v-html="returnShortNoteString()"></span><a href='javascript:;' v-if="isShowReadMoreButtton()" v-on:click="openFullDateModal()">
             &nbsp;&nbsp;{{$t("readmore")}} ...</a>
        <div class="modal d-block my-popups dialog" v-if="isShowFullData">
        <div class="modal-dialog">
        <div class="modal-content">
        <div class="modal-header">
        <h5 class="modal-title">{{heading}}</h5>
        <button type="button" href="javascript:;" class="close" v-on:click="isShowFullData = false;"><em class="fa fa-times"></em></button>
        </div>
        <div class="modal-body crmnote-description">
        <div v-if="callFrom!='Notes'" v-html="tempData" style="max-height: 500px;overflow-y:auto;"></div>
        <ckeditor v-if="callFrom=='Notes'" id="editor-a" :editor="editor" @ready="onReady"  :config="editorConfig" style="height: 400px "></ckeditor>       
        </div>
        </div>
        </div>
        </div>
        </div>
</template>