<style scoped>
.ck-editor__editable {
    min-height: 200px !important;
    border: 1px solid #c1c1c1
}

.validborder {
    border: none !important;
}

.validborder .ck-content {
    border-color: #dc3545 !important;
}
</style>
<template>
    <div class="">
        <div v-bind:class="{ 'feedback-ticket feedback-ticket-present-answersDiv mb-2': true, 'border p-3': (!isShowNoteDialogue || callfrom != 'quickViewAndNoteJS') }"
            v-if="isShowNoteDialogue">
            <div class="heading-border">
                <h5 class="h5 mb-2"> <span> {{ $t("AddNotes") }}</span></h5>
            </div>
            <div class="feedback-ticket-content">
                <Form v-slot="{ values, meta }" ref="form">
                    <div class="feedback-conversation-box" style="padding:none;">
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                                <div class="form-group">
                                    <label>{{ $t("Visibility") }} :</label>
                                    <div class="pl-0 border-0">
                                        <div class="form-check form-check-inline">
                                            <div class="custom-control custom-radio custom-control-inline"><input
                                                    type="radio" v-model="IsPrivate" id="radio_Public"
                                                    class="custom-control-input dynamic" value="0" name="rdo_ForNote"
                                                    checked="checked" /><label class="custom-control-label"
                                                    for="radio_Public"> {{ $t("Public") }} </label>
                                            </div>
                                        </div>
                                        <div class="form-check form-check-inline">
                                            <div class="custom-control custom-radio custom-control-inline"><input
                                                    v-model="IsPrivate" id="radio_Private" type="radio"  
                                                    class="custom-control-input" value="1" name="rdo_ForNote" /><label
                                                    class="custom-control-label" for="radio_Private"> {{ $t("Private") }}
                                                </label></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="conversation-content feedback-conversation-content">                       
                            <Field name="note" :rules="{ 'required': true }" label="Note"
                                v-slot="{ errors }">
                                <ckeditor :editor="editor" v-model="note" ></ckeditor>
                                <span   name="note"
                                    class="invalid-feedback d-block">{{ errors[0] }}</span>
                            </Field>
                        </div>
                    </div>
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 mt-3 text-right">
                        <div class="form-group mb-0">
                            <div class="search-btm-btn">
                                <a href="javascript:;" class="btn btn-success save-btn mr-1"
                                    @click.once="saveNote(values, meta)" title="Save"><em
                                        class="fa fa-save pr-2 text-white"></em>{{ $t("Save") }} </a>
                                <a href="javascript:;" class="btn btn-danger delete-btn"
                                    v-on:click="closeNoteDialogue()" title="Cancel"><em
                                        class="fa fa-close pr-2 text-white"></em>{{ $t("Cancel") }}</a>
                            </div>
                            <div style="clear:both;"></div>
                        </div>
                    </div>
                </Form>
            </div>
        </div>
        <div class="pb-2 d-inline-block" v-if="(!isShowNoteDialogue || callfrom != 'quickViewAndNoteJS')">
            <span class="legend-Progress colorboxsquare"></span>{{ $t('Private') }} {{ $t('NOTE') }}
            <span class="legend-upcoming colorboxsquare ml-3"></span>{{ $t('Public') }} {{ $t('NOTE') }}
        </div>
        <div class="col-12 p-0" v-if="(!isShowNoteDialogue || callfrom != 'quickViewAndNoteJS')">
            <div class="container-fluid p-0">
                <div v-if="NoRecordForTab" class="text-center text-danger p-4 border">{{ NoRecordFoundText }}</div>
                <div style="clear:both;"></div>
                <div v-if="ListTypeForViewChange != 'list'" class="row ticket-card-container card-view bg-border p-0">
                    <div style="min-width:257px" class="col-md-12 col-lg-6 col-xl-3 mb-3" v-for="(item,index) in NoteData"
                        :key="index">
                        <div
                            v-bind:class="{ 'card-box': true, 'blue-bg-light': !item.IS_PRIVATE, 'green-bg-light': item.IS_PRIVATE }">
                            <div class="card-heading d-flex align-items-center">
                                <span class="user-img-con mr-2"><img
                                        v-bind:src="(item.PROFILE_PATH != null && item.PROFILE_PATH != '') ? item.PROFILE_PATH : '/Content/images/uc/uc_user_new.jpg'"
                                        alt="" /></span> {{ item.notes_created_by }}
                            </div>
                            <div class="card-detail-main" style="max-height: 150px;">
                                <div class="card-detail"><em class="fa fa-address-card"
                                        aria-hidden="true"></em><span>{{ item.crm_designation }}</span></div>
                                <div style="clear: both;"></div>
                                <crmreadmore callFrom="Notes" v-bind:data="item.crm_notes" v-html="item.crm_notes" :heading="$t('NOTE')"
                                    length="100"></crmreadmore>
                            </div>
                            <div class="bottom-action">
                                <div class="card-date"><em class="fa fa-calendar-o" aria-hidden="true"></em>
                                    <span> &nbsp;{{item.note_date | formatDateTime }}</span></div>
                                <div class="card-action"
                                    v-if="(userid == item.created_by && item.crm_note_id == editDeleteButonFor)">
                                    <a href="javascript:;"
                                        v-if="(userid == item.created_by && item.crm_note_id == editDeleteButonFor)"
                                        class="round-icon-small btn-success-light" v-on:click="editNote(item)">
                                        <em class="fa fa-pencil" aria-hidden="true"></em>
                                    </a>
                                    <a href="javascript:;"
                                        v-if="(userid == item.created_by && item.crm_note_id == editDeleteButonFor)"
                                        class="round-icon-small btn-danger-light" id="DeleteNote"
                                        v-on:click="deleteNote(item.crm_note_id)">
                                        <em class="fa fa-trash" aria-hidden="true"></em>
                                    </a>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="conversation-main" v-if="ListTypeForViewChange == 'list'">
                <template v-for="(item,index1) in NoteData">
                    <div class="conversation-time"><span>{{formatTime(item.note_date) }}</span></div>
                    <div class="conversation-box-con" v-if="!NoRecordForTab">
                        <div
                            v-bind:class="{ 'conversation-box': true, 'public-note': !item.IS_PRIVATE, 'private-note': item.IS_PRIVATE }">
                            <div class="name-circle light-grey"><img class="img-circle avatar img-max-width"
                                    v-bind:src="(item.PROFILE_PATH != null && item.PROFILE_PATH != '') ? item.PROFILE_PATH : '/Content/images/DefaultImages/uc_user_new.jpg'"
                                    alt="" /></div>
                            <div class="conversation-header">
                                <span>
                                    <span class="sender-name">{{ item.notes_created_by }}</span>
                                    <span class="sender-department">(<small class="text-danger font-14"
                                            v-bind:title="item.note_date | formatDateTime">{{ item.note_date | formatDateTime }}</small>)</span>
                                </span>
                                <span class=""><a href="javascript:;"
                                        v-if="(userid == item.created_by && item.crm_note_id == editDeleteButonFor)"
                                        class="round-icon-small btn-danger-light float-right" id="DeleteNote"
                                        v-on:click="deleteNote(item.crm_note_id)">
                                        <em class="fa fa-trash" aria-hidden="true"></em>
                                    </a>
                                    <a href="javascript:;"
                                        v-if="(userid == item.created_by && item.crm_note_id == editDeleteButonFor)"
                                        class="round-icon-small btn-success-light float-right"
                                        v-on:click="editNote(item)">
                                        <em class="fa fa-pencil" aria-hidden="true"></em>
                                    </a></span>
                            </div>
                            <div class="conversation-content">
                                <p v-html="item.crm_notes"></p>
                            </div>
                        </div>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>
<script>
import moment from 'moment';
import crmreadmore from '../Modal/crm-readmore.vue'
import LeadDataService from "../../../services/DataService";
import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
export default {
    components: {
        crmreadmore,
        ClassicEditor
        },
    props: {
        notedata: {
            type: Array,
            required: true
        },
        userid: {
            type: Number,
            required: true
        },
        sourceid:null,
        modulename: {
            type: String,
            required: true
        },
        submodulecode: {
            type: String,
            required: true
        },
        callfrom: {
            type: String
        },
        curIndex: {
            type: Number
        },
        listtype: {
            type: String
        },
        showNotePopUp: {
            type: Boolean
        }
    },
    data: function () {
        return {
            NoteData: [],
            NoRecordForTab: true,
            LeadStatus: this.$parent.LeadStatus,
            isShowNoteDialogue: false,
            note: '',
            crmNoteId: null,
            editDeleteButonFor: 0,
            isShowFulleNote: false,
            FulleNoteToShow: "",
            IsPrivate: "0",
            TotalCount: 0,
            ListTypeForViewChange: 'list',
            NoRecordFoundText: this.$t("Loading"),
            editor: ClassicEditor ,
            userTimeZone: null,

        }
    },
    created: function () {        
        this.userTimeZone = this.GetUserInfo.TimeZone.split(":");
        String.prototype.format = function () {
            var args = arguments;
            return this.replace(/{([0-9]+)}/g, function (match, index) {
                return typeof args[index] == 'undefined' ? match : args[index];
            });
        };
    },
    watch: {
        listtype: function (value) {            
            this.ListTypeForViewChange = value;
        },
        notedata: function (value) {
            var thsObj = this;
            thsObj.ListTypeForViewChange = $.trim(thsObj.listtype) != "" ? thsObj.listtype : thsObj.ListTypeForViewChange;
            thsObj.TotalCount = value.length;
            thsObj.NoteData = value;
            thsObj.NoRecordForTab = (thsObj.NoteData != null && thsObj.NoteData.length > 0) ? false : true;
            if (thsObj.NoRecordForTab) {
                thsObj.NoRecordFoundText = thsObj.$t("NoRecordfound");
            }
            thsObj.isShowNoteDialogue = thsObj.showNotePopUp;   
            thsObj.crmNoteId = null;
            thsObj.note = '';
            //thsObj.IsPrivate = "0";
            if (thsObj.NoteData != null && thsObj.NoteData.length > 0) {
                var tmpData = thsObj.NoteData.filter(function (obj) { return obj.created_by == thsObj.userid });
                if (tmpData.length > 0) {
                    thsObj.editDeleteButonFor = tmpData[0].crm_note_id;
                }
            }
        }
    },
    methods: {
        onReady(editor) {
            editor.ui.getEditableElement().parentElement.insertBefore(
                editor.ui.view.toolbar.element,
                editor.ui.getEditableElement()
            );
        },
        formatTime:function (value) { 
            if (value) {
            try {
                var timeFormat = 'h:mm A';
                timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
             
                return moment(getDate, "h:mm A").format(String(timeFormat));
            } catch (e) {
                
                timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                return moment(String(value)).format(String('h:mm A').toUpperCase() + " " + String(timeFormat));
            }
        }
            
    },
        openNoteDialogue: function () {
            if ((typeof this.$parent.checkForAdditionalPermission !== 'undefined') && !this.$parent.checkForAdditionalPermission("AddNote")) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, this.$t('Alert'));
            } else {
                this.isShowNoteDialogue = true;
                this.IsPrivate = "0";
                this.NoRecordForTab = (this.isShowNoteDialogue) ? false : true;
            }
        },
        closeNoteDialogue: function () {
            var thsObj = this;
            thsObj.note = '';
            thsObj.IsPrivate = "0";
            if (thsObj.callfrom == "quickViewAndNoteJS") {
                console.log(thsObj.$parent);
                thsObj.$parent.isShowNotePopup = false;
                const urlParams = new URLSearchParams(window.location.search);
                var UrlParam = urlParams.get('reqfrom');
                if (UrlParam == null)
                    UrlParam = 0;
                var ReqFrom = parseInt(UrlParam) || 0;
                if (ReqFrom > 0) {
                    thsObj.$parent.loadNoteData(thsObj.sourceid);
                }
            } else {
                thsObj.$parent.loadData('notes');
            }
            $('.AddNote').removeClass("d-none");
            thsObj.isShowNoteDialogue = false;
            thsObj.NoRecordForTab = (thsObj.NoteData != null && thsObj.NoteData.length > 0) ? false : true;
            thsObj.crmNoteId = null;
          
        },
        saveNote: function (values, meta) {
            debugger;
            var vm = this;
            // values['note']=vm.note;       
            const urlParams = new URLSearchParams(window.location.search);
            var UrlParam = urlParams.get('reqfrom');
            if (UrlParam == null) {
                if (window.location.pathname.indexOf('Listing') > -1 || window.location.pathname.indexOf('Index') > -1) {
                    UrlParam = 1;
                } else {
                    UrlParam = 0;
                }
            }
            var ReqFrom = parseInt(UrlParam) || 0;
            this.$refs.form.validate().then((result) => {
                if (result) {
                    var data = {
                        sourceId: vm.sourceid,
                        moduleName: vm.modulename,
                        subModuleCode: vm.submodulecode,
                        note: vm.note,
                        isPrivate: vm.IsPrivate,
                        crmNotesId: vm.crmNoteId
                    };
                    var objectPostString = JSON.stringify(data);
                    LeadDataService.SaveNoteData(objectPostString).then(function (response) {
                        if (response.data[0].Status == "Success") {
                            if (vm.submodulecode == "CRM_LEADS" && ReqFrom > 0)
                                vm.$parent.leadInfo.note_count = vm.TotalCount + 1;
                            if (vm.submodulecode == "CRM_DEALS" && ReqFrom > 0)
                                vm.$parent.DealInfo.NOTE_COUNT = vm.TotalCount + 1;
                            vm.closeNoteDialogue();
                            vm.ShowAlert(vm.$t('InterviewStageAdded', [vm.$t('Note')]), "Success", true, vm.$t("Alert"));
                            if (ReqFrom > 0) {
                                vm.$parent.loadNoteData(vm.sourceid);
                            }
                            else {
                                vm.$parent.loadData('notes', 'savenote');
                            }
                            vm.note = "";
                            vm.isShowNoteDialogue = false;
                            $('.AddNote').removeClass("d-none");
                        }

                    })
                }
            })
        },
        editNote: function (item) {
            $('#DeleteNote').addClass("disabled");
            $('.AddNote').addClass("d-none");
            var thsObj = this;
            thsObj.isShowNoteDialogue = true;
            thsObj.note = item.crm_notes;
            thsObj.IsPrivate = (item.IS_PRIVATE) ? "1" : "0";
            thsObj.crmNoteId = item.crm_note_id;
        },
        deleteNote: function (id) {
            var vm = this;
            const urlParams = new URLSearchParams(window.location.search);
            var UrlParam = urlParams.get('reqfrom');
            if (UrlParam == null) {
                if (window.location.pathname.indexOf('Listing') > -1 || window.location.pathname.indexOf('Index') > -1) {
                    UrlParam = 1;
                } else {
                    UrlParam = 0;
                }
            }
            var ReqFrom = parseInt(UrlParam) || 0;
            vm.confirmR(this.$t("ConfirmDelete"), this.$t("Delete"), this.$t("Note"), function () {
                var postJSON = {
                    ids: id,
                    moduleName: vm.modulename,
                    subModuleCode: vm.submodulecode,
                    refCode: 'DELETE_VIEW_CRM_NOTE'

                };
                var objectPostString = JSON.stringify(postJSON);
                LeadDataService.CommonDelete(objectPostString).then(function (response) {
                    var box = "";
                    if (response.data[0].Status == "Success") {
                        if (vm.submodulecode == "CRM_LEADS" && ReqFrom > 0)
                            vm.$parent.$parent.leadInfo.note_count = vm.TotalCount - 1;
                        if (vm.submodulecode == "CRM_DEALS" && ReqFrom > 0)
                            vm.$parent.$parent.DealInfo.note_count = vm.TotalCount - 1;
                        if (vm.callfrom == "quickViewAndNoteJS" && ReqFrom > 0) {
                            vm.$parent.$parent.loadNoteData(vm.sourceid);
                        } else {
                            vm.$parent.loadData('notes');
                        }
                        box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t("NOTE")]), "success", true, vm.$t("Alert"));
                    }
                    else {
                        box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t("NOTE")]), "failure", true, vm.$t("Alert"));
                    }
                    setTimeout(function () {
                        box.modal('hide');
                    }, 2000);

                });

            })

        },
        returnShortNoteString: function (note, len) {
            var el = document.implementation.createHTMLDocument().createElement('div');
            el.innerHTML = note;
            noteHtmlData = el.innerText;
            if (noteHtmlData.length <= len) return note;
            return noteHtmlData.substring(0, len);
        },
        isShowReadMoreButtton: function (note, len) {
            var el = document.implementation.createHTMLDocument().createElement('div');
            el.innerHTML = note;
            noteHtmlData = el.innerText;
            if (noteHtmlData.length <= len) return false;
            return true;
        },
        openFullNoteModal: function (note) {
            this.FulleNoteToShow = note;
            this.isShowFulleNote = true;
        }
    },
}
</script>


